












































import Vue from 'vue'

export default Vue.extend({
  name: 'competences',
  methods: {
    setCompetences: function () {
      let competencesDescription: any = this.$refs.competencesDescription
      competencesDescription.style.opacity = '1'
      competencesDescription.style.marginLeft = '0'

      let competencesGraph: any = this.$refs.competencesGraph
      competencesGraph.style.width = '100%'
      competencesGraph.style.height = '100%'
      // competencesGraph > div > h3
      competencesGraph.children['0'].children['0'].style.opacity = '1'

      let competencesOptions: any = this.$refs.competencesOptions
      let arrayMeters: any = competencesOptions.getElementsByClassName('competences-option-meter')

      for (let index: number = 0; index < arrayMeters.length; index++) {
        arrayMeters[index].className += ` trigger-meter-${index + 1}`
      }
    }
  }
})
