import { Dictionary } from 'vue-router/types/router'

export default Vue.extend({
  name: 'projects',
  methods: {
    setProjects: function () {
      let projectsTitle: any = this.$refs.projectsTitle
      projectsTitle.style.opacity = '1'
      projectsTitle.style.marginLeft = '0'

      let project: any = this.$refs.project

      for (let index: number = 0; index < project.length; index++) {
        project[index].style.opacity = '1'
      }
    }
  },
  data () {
    return {
      projectsArray: projectsArray
    }
  }
})

