



















import Vue from 'vue'

export default Vue.extend({
  name: 'about-me',
  methods: {
    setAboutMe: function () {
      let aboutMe: any = this.$refs.aboutMe
      aboutMe.style.opacity = '1'
      aboutMe.children['0'].style.marginLeft = '0'
      aboutMe.children['1'].style.marginLeft = '20px'
    },
    showExtra: function () {
      let element: any = this.$refs.aboutMeExtra
      element.style.opacity = '1'
    }
  }
})
